.sidebar {
  flex: 3;
  height: calc(100vh - 50px);
  /* background-color: blue; */
  overflow-y: scroll;
  position: sticky;
  top: 50px;
}
::-webkit-scrollbar {
  width: 5px;
}
::-webkit-scrollbar-track {
  background-color: #f1f1f1;
}
::-webkit-scrollbar-thumb {
  background-color: rgba(179, 179, 179);
}
.sidebarWrapper {
  padding: 20px;
}
.sidebarList {
  padding: 0;
  margin: 0;
  list-style: none;
}
.sidebarListitem {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}
.sidebarIcon {
  margin-right: 15px;
}
.sidebarButton {
  width: 150px;
  border: none;
  padding: 10px;
  border-radius: 5px;
  font-weight: 500;
}
.sidebarHr {
  margin: 20px 0;
}
.sidebarFriendList {
  padding: 0;
  margin: 0;
  list-style: none;
}
.findFrinedBtn {
  margin-top: 5px;
  margin-bottom: 5px;
  padding: 5px 10px;
  color: white;
  background-color: #1872f2;
  border: none;
  border-radius: 5px;
  display: inline-flex;
  align-items: center;
  font-weight: 500;
  cursor: pointer;
}

/* /////////////////////////////////////// */
@media (max-width: 1000px) {
  .sidebar {
    display: none;
  }
}
