.login {
  width: 100vw;
  height: 100vh;
  background-color: #f0f2f5;
  display: flex;
  align-items: center;
  justify-content: center;
}
.loginWrapper {
  width: 70%;
  height: 70%;
  display: flex;

  gap: 1rem;
}
.loginLeft,
.loginRight {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.loginLogo {
  font-size: 50px;
  font-weight: 800;
  color: #1775ee;
  margin-bottom: 10px;
}
.loginDesc {
  font-size: 24px;
}

.loginBox {
  height: 400px;
  padding: 20px;
  background-color: white;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.logininput {
  height: 50px;
  border-radius: 10ox;
  border: 1px solid grey;
  font-size: 18px;
  padding-left: 20px;
}
.logininput:focus {
  outline: none;
}
.loginButton {
  height: 50px;
  border-radius: 10px;
  border: none;
  background-color: #1775ee;
  color: white;
  font-size: 20px;
  font-weight: 500;
  cursor: pointer;
}
.loginForgot {
  text-align: center;
  color: #1775ee;
}
.loginRegisterButton {
  width: 100%;
  align-self: center;
  height: 50px;
  border-radius: 10px;
  border: none;
  background-color: #42b72a;
  color: white;
  font-size: 20px;
  font-weight: 500;
  cursor: pointer;
}
/* ///////////////////////////// */
@media (max-width: 1000px) {
  .loginWrapper {
    width: 80%;
    height: 70%;
    display: flex;
  }
}
@media (max-width: 900px) {
  .loginWrapper {
    width: 90%;
    height: 70%;
    display: flex;
  }
}
@media (max-width: 700px) {
  .loginWrapper {
    width: 95%;
    height: 70%;
    display: flex;
  }
}
@media (max-width: 600px) {
  .loginWrapper {
    width: 70%;
    height: 70%;
    display: flex;
    flex-direction: column;
  }
  .loginLeft,
  .loginRight {
    margin-bottom: 0px;
  }
  .loginLogo {
    text-align: center;
  }
  .loginDesc {
    text-align: center;
  }
}
