.topbarContainer {
  height: 50px;
  width: 100vw;
  background-color: #1877f2;
  display: flex;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 999;
}
.topbarLeft {
  flex: 3;
}
.topbaricon {
  font-size: 35px;
  margin-left: 20px;
  font-weight: bold;
  color: white;
  cursor: pointer;
  letter-spacing: 0.5px;
}

.topbarCenter {
  flex: 3.5;
}
.searchbar {
  max-width: 100%;
  height: 30px;
  background-color: white;
  border-radius: 30px;
  display: flex;
  align-items: center;
}
.searchIcon {
  font-size: 20px important;
  margin-left: 10px;
}
.searchInput {
  border: none;
  width: 70%;
}
.searchInput:focus {
  outline: none;
}
.topbarRight {
  flex: 5.5;
  display: flex;
  align-items: center;
  justify-content: space-around;
  color: #fff;
}

.topbarLink {
  margin-right: 10px;
  font-size: 14px;
  cursor: pointer;
}
.topbarIcons {
  display: flex;
}
.topbarIcoItem {
  margin-right: 15px;
  cursor: pointer;
  position: relative;
}

.topbarIconBadge {
  width: 15px;
  height: 15px;
  background-color: red;
  border-radius: 50%;
  color: #fff;
  position: absolute;
  top: -5px;
  right: -5px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
}
.topbarImg {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  object-fit: cover;
  cursor: pointer;
}
.logoutBtn {
  display: flex;
  /* flex-direction: column; */
  align-items: center;
  font-size: 15px;
  margin-right: 30px;
  line-height: 5px;
  cursor: pointer;
}
.topBarOpenIcon {
  margin-right: 10px;
  display: none;
  position: relative;
  background-color: #1877f2;
  border: none;
  color: #fff;
}
.closeIcon {
  position: relative;
  top: 0;
  display: none;
}
.hide {
  display: none;
}
.topbarOpen {
  position: absolute;
  top: 48px;
  right: 0px;
  display: none;
  cursor: pointer;
}
.topbarOpenLink {
  color: #fff;
  background-color: black;
  min-width: 250px;
  height: 80px;
  font-size: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid #706e6eeb;
  cursor: pointer;
}
.topbarOpenLink:hover {
  color: #fff;
  background-color: #1872f2;
}
.show {
  display: block;
}

/* ///////////////////////// */

@media (max-width: 1050px) {
  .logoutBtn .logout {
    display: none;
    margin-left: 10px;
  }

  .searchbar {
    max-width: 90%;
  }
  .topbarLeft {
    flex: 3;
  }
  .topbarCenter {
    flex: 3;
  }
  .topbarRight {
    flex: 6;
  }
}
@media (max-width: 860px) {
  .logoutBtn .logout {
    display: none;
  }

  .searchbar {
    max-width: 100%;
  }

  .topbarCenter {
    flex: 4;
  }
  .topbarRight {
    flex: 6;
  }
  .topbarLinks {
    display: none;
  }
  .topBarOpenIcon {
    display: block;
  }

  .topbarContainer {
    width: 100vw;
  }
  .logoutBtn {
    /* min-width: 250px;
    height: 80px; */
    display: none;
  }
}
@media (max-width: 500px) {
  .logoutBtn .logout {
    display: none;
  }

  .searchbar {
    max-width: 100%;
  }

  .topbarCenter {
    flex: 1;
    display: none;
  }
  .topbarRight {
    flex: 6;
  }
  .topbarLinks {
    display: none;
  }
  .topBarOpenIcon {
    display: block;
  }

  .topbarContainer {
    width: 100vw;
  }
  .logoutBtn {
    /* min-width: 250px;
    height: 80px; */
    display: none;
  }
}
