.all {
  position: absolute;
}
.body {
  width: 100vw;
  height: 100vh;
  display: flex;
}
.sideBar {
  flex: 3;
}
.middle {
  flex: 6;
  padding: 5rem 10rem;
}
.rightBar {
  flex: 3;
}

/* ////////////////////////////////// */

.editInfoContainer {
  position: relative;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2;
}
.finfeditInfo {
  width: 1000px;
  height: 600px;
  background-color: #fff;
}

/* //////////////////////// */
@media (min-width: 1300px) {
  .sideBar {
    flex: 3.5;
  }
  .middle {
    flex: 4.5;
  }
  .rightBar {
    flex: 4;
  }
}
@media (max-width: 1000px) {
  .sideBar {
    flex: 2;
  }
  .middle {
    flex: 8;
    padding: 5rem 9rem;
  }
  .rightBar {
    flex: 2;
  }
}
@media (max-width: 860px) {
  .sideBar {
    flex: 1;
  }
  .middle {
    flex: 10;
  }
  .rightBar {
    flex: 1;
  }
}
@media (max-width: 760px) {
  .sideBar {
    flex: 0;
  }
  .middle {
    flex: 12;
    /* padding: 5rem 5rem; */
  }
  .rightBar {
    flex: 0;
  }
}
@media (max-width: 600px) {
  .body {
    display: flex;
    justify-content: center;
  }
  .sideBar {
    flex: 0;
  }
  .middle {
    flex: 12;
    display: flex;
    margin: 0 auto;
  }
  .rightBar {
    flex: 0;
  }
  .sidebarFriendList {
    width: 100vw;
  }
  /* .sidebarFriend {
    padding: 2rem;
  } */
}
