.profile {
  display: flex;
}
.profileRight {
  flex: 9;
}
.profileCover {
  height: 320px;
  position: relative;
}
.profileCoverImg {
  width: 100%;
  height: 250px;
  object-fit: cover;
}
.profileUserImg {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  object-fit: cover;
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  top: 150px;
  border: 3px solid white;
}
.profileInfo {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
}
.editButton {
  display: flex;
  /* width: 500px; */
  justify-content: space-around;
}
.profileInfoName {
  font-size: 24px;
}
.profileInfoDesc {
  font-weight: 300;
}
.profileRightBottom {
  display: flex;
}
.editInfoContainer {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  visibility: hidden;
  /* display: none; */
}
.editInfo {
  position: absolute;
  border: 2px solid black;
  display: flex;
  align-items: center;
  justify-content: center;
  visibility: hidden;
  top: 60px;
  background-color: white;
  padding: 5rem;
  width: 700px;
}
.container {
  width: 100vw;
  position: absolute;
}
.openEdit {
  visibility: visible;
  /* display: block; */
}
.closeBtn {
  margin-bottom: 5px;
  color: black;
  border: none;
  display: inline-flex;
  align-items: cnter;
  font-weight: 500;
  cursor: pointer;
  position: absolute;
  top: 0;
  right: 0;
}
/* ///////////////////////// */
@media (max-width: 1000px) {
  .editInfo {
    width: 550px;
  }
}
@media (max-width: 800px) {
  .editInfo {
    width: 400px;
  }
}
@media (max-width: 650px) {
  .editInfo {
    width: 250px;
  }
}
@media (max-width: 500px) {
  .editInfo {
    width: 150px;
  }
  .editButton {
    justify-content: space-between;
  }
}
