.rightbar {
  flex: 3.5;
}
.rightbarWrapper {
  padding: 20px 20px 0 10px;
}
.birthdayContainer {
  display: flex;
  align-items: center;
}
.birthdayimg {
  width: 40px;
  height: 40px;
  margin-right: 10px;
}
.birthdayText {
  font-weight: 300;
  font-size: 15px;
}
.rightbarAd {
  width: 100%;
  border-radius: 10px;
  margin: 30px 0;
}
.rightbarTitle {
  margin-bottom: 20px;
}
.rightbarFriendList {
  padding: 0;
  margin: 0;
  list-style: none;
}
.rightbarTitle {
  font-size: 18px;
  font-weight: 500;
  margin-bottom: 10px;
}
.rightbarInfo {
  margin-bottom: 30px;
}
.rightbarInfoItem {
  margin-bottom: 10px;
}
.rightbarInfoKey {
  font-weight: 500;
  margin-right: 15px;
  color: #555;
}
.rightbarInfoValue {
  font-weight: 300;
}
.rightbarFollowings {
  display: flex;
  flex-wrap: wrap;
  /* justify-content: space-between; */
}
.rightbarFollowing {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
  cursor: pointer;
  margin-right: 7px;
}
.rughtbarFollowingImg {
  width: 100px;
  height: 100px;
  object-fit: cover;
  border-radius: 5px;
}
.rightbarFollowButton {
  margin-top: 30px;
  margin-bottom: 10px;
  padding: 5px 10px;
  color: white;
  background-color: #1872f2;
  border: none;
  border-radius: 5px;
  display: inline-flex;
  align-items: center;
  font-weight: 500;
  cursor: pointer;
}
/* ///////////////////////////// */
@media (max-width: 590px) {
  .rightbar {
    display: none;
  }
}
