.sidebarFriend {
  width: 100%;
  display: flex;
  margin-bottom: 10px;
  gap: 2px;
  justify-content: space-between;
  align-items: center;
}
.user {
  display: flex;
}
.dp {
  display: flex;
  align-items: center;
}
.details {
  display: flex;
  gap: 20px;
  align-items: center;
  /* flex-direction: column; */
}
.city {
  display: flex;
  align-items: center;
}
.sidebarFriendImg {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 10px;
}
.viewProfileBtn {
  margin-top: 5px;
  margin-bottom: 5px;
  padding: 5px 10px;
  color: white;
  background-color: #1872f2;
  border: none;
  border-radius: 5px;
  display: inline-flex;
  align-items: center;
  font-weight: 500;
  cursor: pointer;
}
